import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';


const DEFAULT_AMOUNT = 28000 + 46400
const TOKENS = {
  9490820: 'KEY',
  50497: 'AE',
  49653: 'BTC',
  49681: 'SNT',
  3756934: 'LEND',
  1712771: 'FUEL',
  8520497: 'MTX',
  6771938: 'DEW'
}
const RED_COLOR = '#ea2870'
const GREEN_COLOR = '#70a700'

class App extends Component {

  state = {
    isLoading: false,
    priceList: {},
    records: []
  }

  componentDidMount() {
    const  _this = this
    this.fetchExchangeRecords().then(res => {
      const data = res.data
      _this.setState({
        records: data
      })
      _this.syncTicker()
    })

    this.login()
    this.handleKeyboard()
  }

  handleKeyboard(){
    const _this = this
    document.body.addEventListener('keydown', function(e) {
      if (e.which === 16) {
        _this.multiAction = true
      }
    })
    document.body.addEventListener('keyup', function(e) {
      if (e.which === 16) {
        _this.multiAction = false
      }
    })
  }

  syncTicker() {
    const _this = this
    const ws = new WebSocket('wss://ws.cn-east.mytokenapi.com/ticker')
    ws.onmessage = function(e) {
      const data = JSON.parse(e.data)
      const info = data.data
      if (info && info.tick) {
        const priceList = _this.state.priceList
        priceList[info.tick.symbol] = {
          price: info.tick.price,
          change: info.tick.percent_change_24h
        }
        _this.setState({
          priceList
        })
      }
    }
    ws.onopen = function() {
      ws.send(
        JSON.stringify({"event":"ticker","params":{"ids": Object.keys(TOKENS).map(id => +id)}})
      )
    }
  }

  login() {
    // return fetch('https://www.easy-mock.com/api/u/login', {
    //   method: 'POST',
    //   credentials: 'include',
    //   body: JSON.stringify({name: 'sunylt', password: '123'})
    //   })
  }

  fetchExchangeRecords() {
    return fetch('https://www.easy-mock.com/mock/59d892fd9d342f449f303370/app/records').then(res => res.json())
  }

  filterData = (index) => {
    const data = this.state.records
    if (data[index][4] !== false) {
      data[index][4] = false
    } else {
      data[index][4] = true
    }

    this.setState({
      records: data
    })
    
  }

  checkAll = () => {
    const data = this.state.records
    data.forEach(r => {
      r[4] = true
    })
    this.setState({
      records: data
    })
  }

  render() {
    const _this = this
    const { priceList, records } = this.state
    let remain = 0
    const assetsExchanged = {}
    records.forEach((data, index) => {
    
      if (data[4] === false) {
        return
      }
      remain += parseInt(data[1])

      // 如果有兑换记录
      if (data[2]) {
        const res = data[2].split(/\d+/)
        //const isAdd = res[0] === '+'
        const tokenName = res[1]
        const amount = parseInt(data[2])
        if (typeof assetsExchanged[tokenName] === 'undefined') {
          assetsExchanged[tokenName] = amount
        } else {
          assetsExchanged[tokenName] = assetsExchanged[tokenName] + amount
        }
        if (assetsExchanged[tokenName] === 0) {
          records.forEach((r, i) => {
            let x = r[2].split(/\d+/)
            if (i <= index && res[1] === x[1]) {
              r.completed = true
            }
          })
        }
      }
    })

    const rows = records.map((data, index) => {
      const res = data[2].split(/\d+/)
      const tokenName = res[1]
      const amount = parseInt(data[2])
      let amountToAE = ''
      if (priceList[tokenName] && priceList.AE) {
        amountToAE = parseInt((Math.abs(amount) * priceList[tokenName].price)/priceList.AE.price)
      }
      return (
      <tr key={index}>
        <td>{data[0]}</td>
        <td>{data[1]}</td>
        <td title={amountToAE ? `≈${amountToAE}AE` : ''}>{data[2]}</td>
        <td>
          <span style={{paddingLeft: '3px', color: data[1] < 0 && amountToAE - Math.abs(data[1]) > 0  ? GREEN_COLOR : RED_COLOR}}>
            { amountToAE && !data.completed ? (data[1] < 0 && amountToAE - Math.abs(data[1]) > 0  ? '+' : '') + (amountToAE - Math.abs(data[1])) : '' }
          </span>
          <input type="checkbox"  checked={data[4] !== false ? true: false} onChange={() => _this.filterData(index)} />
        </td>
        <td>{data[3]}</td>
    </tr>)})

    let exchangedValue = 0
    let priceSyncCount = 0
    let needPrice = 0
    Object.keys(assetsExchanged).forEach(tokenSymbol => {
      if (assetsExchanged[tokenSymbol] > 0) {
        needPrice +=1
        if (this.state.priceList[tokenSymbol]) {
          exchangedValue += assetsExchanged[tokenSymbol] * this.state.priceList[tokenSymbol].price
          priceSyncCount += 1
        }
      }
    })

    const priceSyncOK = priceSyncCount === needPrice
    const canShowPrice = priceList.AE && priceSyncOK
    //const btc = priceList.BTC && canShowPrice ? (exchangedValue + remain * priceList.AE)/priceList.BTC : ''

    window.assetsExchanged = assetsExchanged
    window.records = records
    return (
      <div className="App">
        {/* <div className="login">
          <input type="password" placeholder="请输入密码" />
        </div> */}
        <h2>兑换记录</h2>
        <table border="0" cellPadding="0" cellSpacing="0" width="88%" align="center">
          <thead>
            <tr>
              <th width="18%">DATE</th>
              <th width="20%">SELL/BUY AE</th>
              <th width="20%">SELL/BUY TOKEN</th>
              <th width="22%"><span id="check-all" onClick={this.checkAll}>CHECK ALL</span></th>
              <th width="20%">NOTE</th>
            </tr>
          </thead>
          <tbody>
            { rows }
            <tr>
              <td>SUM</td>
              <td className="ae-total"><b>{remain}</b> AE</td>
              <td><b>{canShowPrice ? parseInt(exchangedValue/priceList.AE.price) : <Loading />}</b> AE</td>
              <td><b>{canShowPrice ? 
                (remain + parseInt(exchangedValue/priceList.AE.price)) + ' (' + (remain + parseInt(exchangedValue/this.state.priceList.AE.price) - DEFAULT_AMOUNT) + ')'
                :
                 <Loading />
              }</b> AE</td>
              <td></td>
            </tr>
          </tbody>
        </table>

        <h2>已兑换资产</h2>
        <table border="0" cellPadding="0" cellSpacing="0" width="88%" align="center" className="exchangedAssets">
            <thead>
              <tr>
                <th width="18%">TOKEN</th>
                <th width="20%">CHANGE(24H)</th>
                <th width="20%">PRICE</th>                
                <th width="22%">AMOUNT</th>
                <th width="20%">BANLANCE</th>
              </tr>
            </thead>
            <tbody>
              {
                Object.keys(assetsExchanged).map(token => {
                    return assetsExchanged[token] > 0 ? <tr key={token}><td>{token}</td><td>{priceList[token] ? <PriceView price={priceList[token].change} /> : <Loading />}</td><td>{ priceList[token] ? '￥' + priceList[token].price.toFixed(5) : <Loading /> }</td><td>{assetsExchanged[token]}</td><td>{priceList[token] ? '￥' + parseInt(priceList[token].price.toFixed(5) * assetsExchanged[token]) : <Loading />}</td></tr> : null
                  })
              }
              <tr>
                <td>AE</td><td>{priceList.AE ? <PriceView price={priceList.AE.change} /> : <Loading />}</td><td>{ priceList.AE ? '￥' + priceList.AE.price.toFixed(5) : <Loading /> }</td><td>{ remain }</td><td>{priceList.AE ? '￥' + parseInt(priceList.AE.price.toFixed(5) * remain) : <Loading />}</td>
              </tr>
            </tbody>
        </table>
      </div>
    );
  }
}

const PriceView = ({price}) => {
  return <span style={{color: +price < 0 ? RED_COLOR : GREEN_COLOR}}>{ price }%</span>
}

const Loading = () => (
  <div className="loadEffect">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
  </div>
)

export default App;
